import React, { Component } from "react";
import { Link } from "react-router-dom";
class Menu extends Component {
        render() {
                return (
                        <div>
                                <ul className="nav nav-tabs bg-dark">
                                        <li className="nav-item">
                                                <Link
                                                        to="/basic-proj/user-dashboard"
                                                        className="nav-link"
                                                >
                                                        User Dashboard
                                                </Link>
                                        </li>
                                </ul>
                        </div>
                );
        }
}
export default Menu;
