import React from "react";
import { connect } from "react-redux";
import Base from "../Basic";
import { Button,
	 CardHeader, 
	 CardBody,
	 Card,
	 Row,
	 Col, } from "reactstrap";
class UserDashboard extends React.Component {
        constructor(props) {
                super(props);
                this.state = {
                        counter: 0,
                };
                console.log("PROPSSS", this.state.counter);
		}
	
        render() {
                return (
                <Row>
                    <Col sm="12">
						<Card  className="pt-4">
							<CardHeader>
									User Dashboard
							</CardHeader>
							<CardBody>
								<Col sm="12">
								<Row>
										<Col sm="12">
										<Button
												onClick={() =>
														console.log(
																"IS AUTHENTICATED",
															this.props.isSuccess
														)
												}
										>
												Counter
										</Button>
										
										</Col>
										<Col sm="12">
											<Button
													onClick={() =>
															console.log(
																	"IS handle",
																	this.props.handleClick()
															)
													}
											>
													Dispatch
													Action
											</Button>
										</Col>
								</Row>
					</Col>
							</CardBody>
					</Card>
                                </Col>
                </Row>
                      
                );
        }
}
const mapStateToProps = (state) => {
        return {
                isSuccess: state.globalReducer.isSuccess,
        };
};
const mapDispatchtoProps = (dispatch) => {
        return {
                handleClick: () => dispatch({ type: "SUCCESS" }),
        };
};
export default connect(mapStateToProps, mapDispatchtoProps)(UserDashboard);
